import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import Headroom from 'react-headroom';
import Navigator from '../components/Navigator';
//import RBootstrap from '../components/temp/react-bootstrap';
import HeaderMaster from '../components/Masthead';
import HeaderChild from '../components/Childhead';
import Footer from '../components/footer';
import store from '../state/store';
//import styled from 'styled-components';
import { Provider } from 'react-redux';
import '../scss/styles.scss';

class TemplateWrapper extends React.Component {
  
  render() {
    let background = null;
    const isHome = this.props.location.pathname == `/`;
    if (isHome) background = <HeaderMaster/>;
    else background = <HeaderChild currentpage={this.props.location.pathname}/>;

    return (
      <div>
        <Helmet
        title="Sysforge: Madhav Shandilya's Portfolio!"
        meta={[
          { name: 'description', content: 'M Shandilya Portfolio' },
          { name: 'keywords', content: 'Madhav Shandilya, Github, Sysforge' },
          { name: 'viewport', content: 'width=device-width, initial-scale=1.0' },
        ]}
      />
      <Headroom disableInlineStyles>
        <header className="header">
          <Navigator />
        </header>
      </Headroom>
      {background}
      {this.props.children()}
      <Footer/>
      </div>
    )
  }
}

const SysforgeLayout = (props, context, c) => {
  return (
    <Provider store={store}>
      <TemplateWrapper {...props} />
    </Provider>
  );
};

export default SysforgeLayout;


