import React from 'react';
import Link from 'gatsby-link';
import YouTube from 'react-youtube';
import Game from "../components/Game"; 
//import HeroLogo from '../images/cross-stitch-site-name.png';
//L24: <h1><img src={HeroLogo} alt="Sysforge Lab" title="Sysforge Lab" /></h1>
//L29: <YouTube videoId='RtEwlMiJoug'/>
import GitHubLogo from '../images/GitHub-Mark-22px.png';
import EinsteinLogo from '../images/einstein-icon.png';
import PropTypes from "prop-types";

export const Childhead = ({props, currentpage}) =>
(
    <header className="sb-page-header">
        <div className="container">
            <ul className="breadcrumb">
                <li><Link to="/">Home</Link></li>                
                <li className="current-breadcrumb">{currentpage}</li>
            </ul>
            <div className="row vertical-align">
                <div className="title-bar-header vertical-align col-sm-9">
                    <h1>{currentpage}</h1>
                </div>
            <Game/>
            </div>
            
        </div>
    </header>
);


class ChildheadFn extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        let childHeaderMsg = null;
        if (this.props.currentpage == '/blog') childHeaderMsg = 'Blog: Cloud Architecture'
        else if (this.props.currentpage == '/mobility') childHeaderMsg = 'Design: Connected Applications'
        else if (this.props.currentpage == '/contact') childHeaderMsg = "Geocode: My Career"
        return (
            <header className="sb-page-header">
                <div className="container">
                    <ul className="breadcrumb">
                        <li><Link to="/">Home</Link></li>                
                        <li className="current-breadcrumb">{this.props.currentpage}</li>
                    </ul>
                    <div className="row vertical-align">
                        <div className="title-bar-header vertical-align col-sm-9">
                            <h1>{childHeaderMsg}</h1>
                        </div>
                    <Game/>
                    </div>
                    
                </div>
            </header>
        )
    }
}


export default ChildheadFn;



