import React from 'react';
import Link from 'gatsby-link';
import ProfileLogo from '../images/profile.jpg';
import FaTerminalLogo from 'react-icons/lib/fa/terminal';
//import SFMobLogo from '../images/salesforce-logo-mobile.png';
import FaCloudLogo from 'react-icons/lib/fa/cloud';
import GitHubLogo from '../images/GitHub-Mark-22px.png';
//aria-expanded="true"

const items = [
  { href: '/blog', label: 'Blog' },
  { href: '/mobility', label: 'Mobility' },
  { href: '/contact', label: 'Contact' },
];

//export default ({ pathname }) => { 
class Navigator extends React.Component {
  constructor(props) {
    super(props);
    this.state = {isToggleOn: false};
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(props) {
    this.setState(prevState => ({
      isToggleOn: !prevState.isToggleOn
    }));
  }

  render(){
    let collapsiblePanel = null;
    if (this.state.isToggleOn) collapsiblePanel = <NavMenu collapsiblePanel="navbar-collapse collapse in" onClick={this.handleClick}/>
    else collapsiblePanel = <NavMenu collapsiblePanel="navbar-collapse collapse" onClick={this.handleClick}/>
    
    function NavMenu(props) {
      const collapsiblePanel = props.collapsiblePanel;
      return (<div id="navbar" className={collapsiblePanel}>
        <ul className="nav navbar-nav">
          {items.map(item =>
            (<li key={item.label}>
            <Link to={item.href} activeClassName="is-active">
              {item.label}
            </Link>
            </li>),
            )
          }
        </ul>
        <ul className="nav navbar-nav navbar-right">
          <li>
            <a className="github-link"
              href="https://github.com/sysforge/sysforge.github.io"
              target="_blank"
              rel="github repository"
            >
            <img src={GitHubLogo} alt="github repository"/>GitHub
            </a>
          </li>
        </ul>
      </div>);
    }

    return (
      <nav className="navbar navbar-inverse navbar-fixed-top">
        <div className="container">
          <div className="navbar-header">
            <a
              className="navbar-brand"
              href="https://www.linkedin.com/in/expressinc/"
              target="_blank"
              rel="linkedin web-opener"
            >
              <FaCloudLogo color='#009ddc' size='40'/>
            </a>
            <a
              className="navbar-brand navbar-brand-mobile"
              href="https://www.linkedin.com/in/expressinc/"
              target="_blank"
              rel="linkedin mobile-opener"
            >
              <FaTerminalLogo color='#009ddc' size='40'/>
            </a>
            <div 
              className="navbar-title">
              <Link to="/" >sysforge</Link>
            </div>
            <button type="button" onClick={this.handleClick} className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
              <span className="sr-only">Toggle navigation</span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
          </div>
          {collapsiblePanel}
        </div>
      </nav>
    )
  }
}

export default Navigator;



